@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

body {
  font-family: "Roboto", sans-serif;
  background: #666;

}

.container {
  padding-top: 5rem;
  width: 800px;
  margin: 0 auto;
}

.card {
  border-radius: 6px;
  background: #fff;
  padding: 1rem;
  box-shadow: 0 5px 1px rgba(0, 0, 0, .2);
  .btn {
    color: crimson;
    position: relative;
    border-radius: 20px;
    border: 1px solid crimson;
    text-decoration: none;
    text-transform: uppercase;
    padding: 0.5rem 1.5rem;
    font-weight: 700;
    outline: none;
    background: #fff;
    transition: all .22s;
    cursor: pointer;
    margin-right: 1rem;
    &.primary {
      background: crimson;
      color: #fff;
    }
    &:active {
      box-shadow: inset 0 3px 1px rgba(220, 20, 60, .2);
    }
    &:hover {
      cursor: pointer;
      opacity: .8;
    }
  }

  input[type=file] {
    display: none;
  }

  .preview {
    display: flex;
    flex-wrap: wrap;
    padding: .5rem;
    &-image {
      position: relative;
      margin: 0 .5rem .5rem 0;
      overflow: hidden;
      &.removing {
        transform: scale(0) rotate(180deg);
        opacity: 0;
        transition: all .35s ease-in 0s;
      }
      img {
        width: 180px;
        height: auto;
        max-height: 200px;
        display: block;
      }
      &:hover {
        .preview-remove {
          transform: translateY(0px);
          transition: transform .22s ease-in 0s;
        }
        .preview-info {
          right: 20px;
          height: 20px;
          transition: all .22s ease-in 0s;
        }
      }
    }

    &-remove {
      transform: translateY(-25px);
      width: 20px;
      height: 20px;
      position: absolute;
      right: 0;
      top: 0;
      font-weight: 700;
      background: #ffffff6f;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      transition: transform .22s ease-in 0s;
    }

    &-info {
      transform: translateY(0px);
      position: absolute;
      right: 0;
      left: 0;
      top: 0;
      height: 25px;
      font-size: .6rem;
      font-weight: 700;
      background: #ffffff6f;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 5px;
      transition: all .22s ease-in 0s;

      &-progress {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        background: #32CD329F;
        text-align: center;
        transition: width .22s;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 1rem;
        &:after {
          content: "%";
        }
      }
    }
  }
}